.header {
  position: relative;
  width: 100%;
  z-index: 100;
}

/* Header Top Styles */
.headerTop {
  background: var(--cultured);
  padding: 10px 0;
  border-bottom: 1px solid var(--cultured);
  animation: slideDown 0.3s ease-out;
}

.container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 15px;
  max-width: 1200px;
  margin: 0 auto;
}

.headerSocialContainer {
  display: flex;
  align-items: center;
  gap: 15px;
  list-style: none;
}

.socialLink {
  color: var(--onyx);
  transition: var(--transition-timing);
  font-size: 20px;
}

.socialLink:hover {
  color: var(--salmon-pink);
}

.headerAlertNews {
  color: var(--sonic-silver);
  font-size: 0.875rem;
  text-transform: uppercase;
}

.headerAlertNews b {
  font-weight: 600;
}

.headerTopActions {
  display: flex;
  align-items: center;
  gap: 15px;
}

.currencySelect,
.languageSelect {
  border: none;
  padding: 5px 10px;
  font-size: 0.875rem;
  color: var(--sonic-silver);
  background: transparent;
  cursor: pointer;
}

.currencySelect:hover,
.languageSelect:hover {
  color: var(--salmon-pink);
}

/* Header Main Styles */
.headerMain {
  padding: 25px 0;
  background: var(--white);
  animation: fadeIn 0.4s ease-out 0.2s both;
}

.headerLogo {
  margin-right: 20px;
}

.headerLogo img {
  width: auto;
  height: 40px;
}

.headerSearchContainer {
  position: relative;
  flex: 1;
  max-width: 600px;
}

.searchField {
  width: 100%;
  padding: 12px 15px;
  padding-right: 50px;
  border: 1px solid var(--cultured);
  border-radius: 4px;
  color: var(--onyx);
  font-size: 0.9375rem;
  transition: all var(--transition-timing);
  background-color: var(--white);
}

.searchField:focus {
  outline: none;
  border-color: var(--salmon-pink);
  box-shadow: 0 0 0 2px rgba(255, 143, 156, 0.1);
}

.searchBtn {
  position: absolute;
  top: 50%;
  right: 2px;
  transform: translateY(-50%);
  color: var(--sonic-silver);
  font-size: 20px;
  padding: 8px 15px;
  border-radius: 4px;
  transition: all var(--transition-timing);
  background: none;
  border: none;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
}

.searchBtn:hover {
  color: var(--salmon-pink);
}

.searchBtn.searching {
  animation: spin 1s linear infinite;
}

.searchResults {
  position: absolute;
  top: calc(100% + 5px);
  left: 0;
  width: 100%;
  background: var(--white);
  border-radius: 4px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  z-index: 100;
  max-height: 400px;
  overflow-y: auto;
  border: 1px solid var(--cultured);
  animation: scaleIn 0.2s ease-out;
  transform-origin: top center;
}

.searchResultItem {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 12px 15px;
  transition: background-color var(--transition-timing);
  border-bottom: 1px solid var(--cultured);
}

.searchResultItem:last-child {
  border-bottom: none;
}

.searchResultItem:hover {
  background-color: var(--cultured);
}

.productName {
  color: var(--onyx);
  font-size: var(--fs-5);
  font-weight: var(--weight-500);
}

.productPrice {
  color: var(--salmon-pink);
  font-size: var(--fs-5);
  font-weight: var(--weight-600);
}

@keyframes spin {
  from {
    transform: translateY(-50%) rotate(0deg);
  }
  to {
    transform: translateY(-50%) rotate(360deg);
  }
}

/* Responsive Search */
@media (max-width: 768px) {
  .searchResults {
    position: fixed;
    top: auto;
    bottom: 60px; /* Above mobile nav */
    left: 0;
    width: 100%;
    max-height: 60vh;
    border-radius: 0;
    border: none;
    border-top: 1px solid var(--cultured);
  }
}

.headerUserActions {
  display: flex;
  align-items: center;
  gap: 15px;
  margin-left: 20px;
}

.actionBtn {
  position: relative;
  font-size: 24px;
  color: var(--sonic-silver);
  padding: 5px;
  transition: color 0.3s ease;
}

.actionBtn:hover {
  color: var(--salmon-pink);
}

.count {
  position: absolute;
  top: -2px;
  right: -3px;
  background: var(--salmon-pink);
  color: var(--white);
  font-size: 12px;
  font-weight: 500;
  line-height: 1;
  padding: 2px 4px;
  border-radius: 20px;
}

/* Responsive Styles */
@media (max-width: 768px) {
  .headerSocialContainer,
  .headerAlertNews {
    display: none;
  }
  
  .headerTopActions {
    margin-left: auto;
  }

  .container {
    flex-wrap: wrap;
    gap: 15px;
  }

  .headerLogo {
    margin-right: auto;
  }

  .headerSearchContainer {
    order: 3;
    width: 100%;
    max-width: 100%;
  }
} 

/* Desktop Navigation Styles */
.desktopNav {
  padding: 15px 0;
  border-bottom: 1px solid var(--cultured);
  background: var(--white);
  animation: slideDown 0.3s ease-out 0.4s both;
}

.desktopMenuList {
  display: flex;
  align-items: center;
  gap: 30px;
  list-style: none;
}

.menuItem {
  position: relative;
}

.menuTitle {
  color: var(--onyx);
  font-size: 0.875rem;
  font-weight: 500;
  text-transform: uppercase;
  padding: 5px;
  transition: color 0.3s ease;
  display: flex;
  align-items: center;
  gap: 5px;
}

.menuTitle:hover,
.menuTitle:focus {
  color: var(--salmon-pink);
  outline: none;
}

.menuTitle:focus-visible {
  outline: 2px solid var(--salmon-pink);
  outline-offset: 2px;
  border-radius: 2px;
}

.dropdownIndicator {
  font-size: 16px;
  transition: transform 0.3s ease;
}

.menuItem:hover .dropdownIndicator,
.menuTitle:focus .dropdownIndicator {
  transform: rotate(-180deg);
}

/* Dropdown Panel Styles */
.dropdownPanel {
  position: absolute;
  top: 100%;
  left: 0;
  background: var(--white);
  width: 100%;
  min-width: 800px;
  padding: 30px;
  border-radius: 0 0 4px 4px;
  border: 1px solid var(--cultured);
  box-shadow: 0 3px 5px rgba(0, 0, 0, 0.1);
  opacity: 0;
  visibility: hidden;
  transform: translateY(10px);
  transition: all var(--transition-timing);
  z-index: 10;
  animation: none;
  transition: opacity 0.2s ease-out, visibility 0.2s ease-out, transform 0.2s ease-out;
}

.dropdownPanel.active,
.menuItem:hover .dropdownPanel {
  opacity: 1;
  visibility: visible;
  transform: translateY(0);
  animation: scaleIn 0.2s ease-out;
}

.dropdownList {
  display: flex;
  gap: 30px;
  list-style: none;
}

.dropdownItem {
  flex: 1;
}

.dropdownTitle {
  color: var(--onyx);
  font-size: 1rem;
  font-weight: 600;
  margin-bottom: 15px;
}

.submenuList {
  list-style: none;
}

.submenuList li {
  margin-bottom: 8px;
}

.submenuList a {
  color: var(--sonic-silver);
  font-size: 0.875rem;
  transition: color 0.3s ease;
}

.submenuList a:hover {
  color: var(--salmon-pink);
}

.dropdownBanner {
  margin-top: 20px;
}

.dropdownBanner img {
  width: 100%;
  height: auto;
  border-radius: 4px;
}

/* Hide desktop nav on mobile */
@media (max-width: 768px) {
  .desktopNav {
    display: none;
  }
} 

/* Mobile Bottom Navigation */
.mobileBottomNav {
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 60px;
  background: var(--white);
  display: none;
  justify-content: space-around;
  align-items: center;
  padding: 0 15px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  z-index: 5;
}

/* Mobile Navigation Menu */
.mobileNav {
  position: fixed;
  top: 0;
  left: -100%;
  width: 100%;
  max-width: 320px;
  height: 100vh;
  background: var(--white);
  padding: 20px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  overflow-y: auto;
  overscroll-behavior: contain;
  visibility: hidden;
  transition: transform 0.3s ease-out, visibility 0.3s ease-out;
  z-index: 20;
  -webkit-overflow-scrolling: touch; /* Smooth scrolling on iOS */
}

.mobileNav.active {
  left: 0;
  visibility: visible;
  animation: slideUp 0.3s ease-out;
}

.mobileNavHeader {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 30px;
}

.menuTitle {
  color: var(--onyx);
  font-size: 1.125rem;
  font-weight: 600;
}

.menuCloseBtn {
  color: var(--sonic-silver);
  font-size: 24px;
  background: none;
  border: none;
  cursor: pointer;
}

.menuCloseBtn:hover {
  color: var(--salmon-pink);
}

.mobileMenuList {
  margin-bottom: 30px;
}

.mobileMenuItem {
  border-bottom: 1px solid var(--cultured);
}

.mobileMenuTitle {
  color: var(--onyx);
  font-size: 0.875rem;
  font-weight: 500;
  padding: 12px 0;
}

/* Accordion Menu */
.accordionMenu {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 12px 0;
  border: none;
  background: none;
  cursor: pointer;
  transition: background-color 0.2s ease-out;
}

.accordionMenu .mobileMenuTitle {
  padding: 0;
}

.accordionIcon {
  font-size: 20px;
  color: var(--sonic-silver);
  transition: transform 0.2s ease-out;
}

.submenuCategory {
  display: none;
  padding-left: 15px;
  margin: 0 0 15px;
  transition: max-height 0.3s ease-out;
  max-height: 0;
  overflow: hidden;
}

.submenuCategory.active {
  display: block;
  max-height: 500px;
}

.submenuCategory li {
  margin-bottom: 8px;
}

.submenuCategory a,
.submenuCategory button {
  color: var(--sonic-silver);
  font-size: 0.875rem;
  font-weight: 400;
  transition: color 0.3s ease;
  background: none;
  border: none;
  cursor: pointer;
  width: 100%;
  text-align: left;
  padding: 4px 0;
}

.submenuCategory a:hover,
.submenuCategory button:hover,
.submenuCategory button.active {
  color: var(--salmon-pink);
}

/* Mobile Menu Bottom */
.mobileMenuBottom {
  margin-top: auto;
  padding-top: 30px;
  border-top: 1px solid var(--cultured);
}

.mobileMenuCategory {
  margin-bottom: 30px;
}

.mobileMenuSocial {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 20px;
  list-style: none;
}

.mobileMenuSocial .socialLink {
  font-size: 20px;
}

/* Overlay */
.overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  background: rgba(0, 0, 0, 0.5);
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
  transition: opacity 0.3s ease-out, visibility 0.3s ease-out;
  z-index: 15;
  backdrop-filter: blur(4px);
}

.overlay.active {
  opacity: 1;
  visibility: visible;
  pointer-events: all;
}

/* Show mobile navigation on smaller screens */
@media (max-width: 768px) {
  .mobileBottomNav {
    display: flex;
  }

  .overlay.active {
    opacity: 1;
    pointer-events: all;
  }
} 

/* Improve focus styles for all interactive elements */
.searchField:focus-visible,
.searchBtn:focus-visible,
.actionBtn:focus-visible,
.currencySelect:focus-visible,
.languageSelect:focus-visible {
  outline: 2px solid var(--salmon-pink);
  outline-offset: 2px;
  border-radius: 4px;
}

/* Mobile menu focus improvements */
.mobileMenuTitle:focus-visible,
.accordionMenu:focus-visible {
  outline: none;
  box-shadow: 0 0 0 2px var(--white), 0 0 0 4px var(--salmon-pink);
  border-radius: 4px;
}

/* Skip to main content link */
.skipLink {
  position: fixed;
  top: -100%;
  left: 0;
  background: var(--salmon-pink);
  color: var(--white);
  padding: 8px 16px;
  z-index: 1000;
  transition: top 0.3s ease;
}

.skipLink:focus {
  top: 0;
}

/* Improved mobile menu animations */
.mobileNav {
  transform: translateX(-100%);
  transition: transform var(--transition-timing), visibility var(--transition-timing);
}

.mobileNav.active {
  transform: translateX(0);
}

/* Improved hover and focus states for search results */
.searchResultItem {
  position: relative;
  overflow: hidden;
}

.searchResultItem::after {
  content: '';
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 2px;
  background-color: var(--salmon-pink);
  transform: scaleX(0);
  transition: transform var(--transition-timing);
  transform-origin: left;
}

.searchResultItem:hover::after,
.searchResultItem:focus::after {
  transform: scaleX(1);
}

/* Improved mobile bottom navigation */
.mobileBottomNav .actionBtn {
  position: relative;
  overflow: hidden;
}

.mobileBottomNav .actionBtn::after {
  content: '';
  position: absolute;
  bottom: 0;
  left: 50%;
  width: 4px;
  height: 4px;
  background-color: var(--salmon-pink);
  border-radius: 50%;
  transform: translateX(-50%) scale(0);
  transition: transform var(--transition-timing);
}

.mobileBottomNav .actionBtn:hover::after,
.mobileBottomNav .actionBtn:focus::after {
  transform: translateX(-50%) scale(1);
} 

/* Enhanced Animations */
@keyframes slideDown {
  from {
    opacity: 0;
    transform: translateY(-10px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes slideUp {
  from {
    opacity: 0;
    transform: translateY(10px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@keyframes scaleIn {
  from {
    transform: scale(0.95);
    opacity: 0;
  }
  to {
    transform: scale(1);
    opacity: 1;
  }
}

/* Apply animations to components */
.headerTop {
  animation: slideDown 0.3s ease-out;
}

.headerMain {
  animation: fadeIn 0.4s ease-out 0.2s both;
}

.desktopNav {
  animation: slideDown 0.3s ease-out 0.4s both;
}

.searchResults {
  animation: scaleIn 0.2s ease-out;
  transform-origin: top center;
}

.dropdownPanel {
  animation: none;
  transition: opacity 0.2s ease-out, visibility 0.2s ease-out, transform 0.2s ease-out;
}

.dropdownPanel.active {
  animation: scaleIn 0.2s ease-out;
}

.mobileNav {
  transition: transform 0.3s ease-out, visibility 0.3s ease-out;
}

.mobileNav.active {
  animation: slideUp 0.3s ease-out;
}

/* Interactive hover effects */
.actionBtn {
  transition: transform 0.2s ease-out, color 0.2s ease-out;
}

.actionBtn:hover {
  transform: translateY(-2px);
}

.searchBtn:hover {
  transform: translateY(-50%) scale(1.1);
}

.menuTitle {
  position: relative;
}

.menuTitle::after {
  content: '';
  position: absolute;
  bottom: -2px;
  left: 0;
  width: 100%;
  height: 2px;
  background-color: var(--salmon-pink);
  transform: scaleX(0);
  transition: transform 0.2s ease-out;
  transform-origin: right;
}

.menuTitle:hover::after,
.menuTitle:focus::after {
  transform: scaleX(1);
  transform-origin: left;
}

.dropdownIndicator {
  transition: transform 0.2s ease-out;
}

.menuItem:hover .dropdownIndicator,
.menuTitle:focus .dropdownIndicator {
  transform: rotate(-180deg);
}

/* Mobile menu animations */
.accordionMenu {
  transition: background-color 0.2s ease-out;
}

.accordionMenu:hover {
  background-color: var(--cultured);
}

.accordionIcon {
  transition: transform 0.2s ease-out;
}

.accordionMenu.active .accordionIcon {
  transform: rotate(180deg);
}

.submenuCategory {
  transition: max-height 0.3s ease-out;
  max-height: 0;
  overflow: hidden;
}

.submenuCategory.active {
  max-height: 500px;
}

/* Overlay animation */
.overlay {
  transition: opacity 0.3s ease-out, visibility 0.3s ease-out;
  backdrop-filter: blur(4px);
}

/* Enhanced focus styles */
.menuTitle:focus-visible,
.actionBtn:focus-visible,
.searchBtn:focus-visible {
  outline: none;
  box-shadow: 0 0 0 2px var(--white), 0 0 0 4px var(--salmon-pink);
  border-radius: 4px;
}

/* Loading animation for search */
@keyframes spin {
  to {
    transform: translateY(-50%) rotate(360deg);
  }
}

.searchBtn.searching {
  animation: spin 0.8s linear infinite;
  color: var(--salmon-pink);
}

/* Cart and wishlist count animation */
@keyframes bounce {
  0%, 100% {
    transform: scale(1);
  }
  50% {
    transform: scale(1.2);
  }
}

.count {
  animation: bounce 0.3s ease-out;
}

/* Mobile bottom nav animations */
.mobileBottomNav .actionBtn::before {
  content: '';
  position: absolute;
  top: 0;
  left: 50%;
  width: 4px;
  height: 4px;
  background-color: var(--salmon-pink);
  border-radius: 50%;
  transform: translateX(-50%) scale(0);
  transition: transform 0.2s ease-out;
}

.mobileBottomNav .actionBtn:hover::before,
.mobileBottomNav .actionBtn:focus::before {
  transform: translateX(-50%) scale(1);
} 